	module.exports = {"data":{"postsJson":{"title":"Paul Sent to Rome","questionsTitle":"LEARNING FROM THE PAST","display":"block","bookName":"Acts","ref":"27:1-28:31","html":"<p>   <strong>27</strong><u>1</u>When it was determined that we should sail for Italy, they delivered Paul and certain other prisoners to a centurion named Julius, of the Augustan band. <u>2</u>Embarking in a ship of Adramyttium, which was about to sail to places on the coast of Asia, we put to sea; Aristarchus, a Macedonian of Thessalonica, being with us. <u>3</u>The next day, we touched at Sidon. Julius treated Paul kindly, and gave him permission to go to his friends and refresh himself. <u>4</u>Putting to sea from there, we sailed under the lee of Cyprus, because the winds were contrary. <u>5</u>When we had sailed across the sea which is off Cilicia and Pamphylia, we came to Myra, a city of Lycia. <u>6</u>There the centurion found a ship of Alexandria sailing for Italy, and he put us on board. <u>7</u>When we had sailed slowly many days, and had come with difficulty opposite Cnidus, the wind not allowing us further, we sailed under the lee of Crete, opposite Salmone. <u>8</u>With difficulty sailing along it we came to a certain place called Fair Havens, near the city of Lasea.<br />   <u>9</u>When much time had passed and the voyage was now dangerous, because the Fast had now already gone by, Paul admonished them, <u>10</u>and said to them,</p><h1>PAUL</h1><h3>Sirs, I perceive that the voyage will be with injury and much loss, not only of the cargo and the ship, but also of our lives.</h3><p><u>11</u>But the centurion gave more heed to the master and to the owner of the ship than to those things which were spoken by Paul. <u>12</u>Because the haven was not suitable to winter in, the majority advised going to sea from there, if by any means they could reach Phoenix, and winter there, which is a port of Crete, looking northeast and southeast.<br />   <u>13</u>When the south wind blew softly, supposing that they had obtained their purpose, they weighed anchor and sailed along Crete, close to shore. <u>14</u>But before long, a stormy wind beat down from shore, which is called Euroclydon.* <u>15</u>When the ship was caught, and couldn’t face the wind, we gave way to it, and were driven along. <u>16</u>Running under the lee of a small island called Clauda, we were able, with difficulty, to secure the boat. <u>17</u>After they had hoisted it up, they used cables to help reinforce the ship. Fearing that they would run aground on the Syrtis sand bars, they lowered the sea anchor, and so were driven along. <u>18</u>As we labored exceedingly with the storm, the next day they began to throw things overboard. <u>19</u>On the third day, they threw out the ship’s tackle with their own hands. <u>20</u>When neither sun nor stars shone on us for many days, and no small storm pressed on us, all hope that we would be saved was now taken away.<br />   <u>21</u>When they had been long without food, Paul stood up in the middle of them, and said,</p><h1>PAUL</h1><h3>Sirs, you should have listened to me, and not have set sail from Crete, and have gotten this injury and loss. <u>22</u>Now I exhort you to cheer up, for there will be no loss of life among you, but only of the ship. <u>23</u>For there stood by me this night an angel, belonging to the God whose I am and whom I serve, <u>24</u>saying, ‘Don’t be afraid, Paul. You must stand before Caesar. Behold, God has granted you all those who sail with you.’ <u>25</u>Therefore, sirs, cheer up! For I believe God, that it will be just as it has been spoken to me. <u>26</u>But we must run aground on a certain island.</h3><p>   <u>27</u>But when the fourteenth night had come, as we were driven back and forth in the Adriatic Sea, about midnight the sailors surmised that they were drawing near to some land. <u>28</u>They took soundings, and found twenty fathoms.* After a little while, they took soundings again, and found fifteen fathoms.* <u>29</u>Fearing that we would run aground on rocky ground, they let go four anchors from the stern, and wished for daylight. <u>30</u>As the sailors were trying to flee out of the ship, and had lowered the boat into the sea, pretending that they would lay out anchors from the bow, <u>31</u>Paul said to the centurion and to the soldiers,</p><h1>PAUL</h1><h3>Unless these stay in the ship, you can’t be saved.</h3><p><u>32</u>Then the soldiers cut away the ropes of the boat, and let it fall off.<br />   <u>33</u>While the day was coming on, Paul begged them all to take some food, saying,</p><h1>PAUL</h1><h3>Today is the fourteenth day that you wait and continue fasting, having taken nothing. <u>34</u>Therefore I beg you to take some food, for this is for your safety; for not a hair will perish from any of your heads.</h3><p><u>35</u>When he had said this, and had taken bread, he gave thanks to God in the presence of all, and he broke it, and began to eat. <u>36</u>Then they all cheered up, and they also took food. <u>37</u>In all, we were two hundred seventy-six souls on the ship. <u>38</u>When they had eaten enough, they lightened the ship, throwing out the wheat into the sea. <u>39</u>When it was day, they didn’t recognize the land, but they noticed a certain bay with a beach, and they decided to try to drive the ship onto it. <u>40</u>Casting off the anchors, they left them in the sea, at the same time untying the rudder ropes. Hoisting up the foresail to the wind, they made for the beach. <u>41</u>But coming to a place where two seas met, they ran the vessel aground. The bow struck and remained immovable, but the stern began to break up by the violence of the waves.<br />   <u>42</u>The soldiers’ counsel was to kill the prisoners, so that none of them would swim out and escape. <u>43</u>But the centurion, desiring to save Paul, stopped them from their purpose, and commanded that those who could swim should throw themselves overboard first to go toward the land; <u>44</u>and the rest should follow, some on planks, and some on other things from the ship. So they all escaped safely to the land.<br />   <strong>28</strong><u>1</u>When we had escaped, then they* learned that the island was called Malta. <u>2</u>The natives showed us uncommon kindness; for they kindled a fire, and received us all, because of the present rain, and because of the cold. <u>3</u>But when Paul had gathered a bundle of sticks and laid them on the fire, a viper came out because of the heat, and fastened on his hand. <u>4</u>When the natives saw the creature hanging from his hand, they said to one another,</p><h1>THE PEOPLE OF MALTA</h1><h4>No doubt this man is a murderer, whom, though he has escaped from the sea, yet Justice has not allowed to live.</h4><p><u>5</u>However he shook off the creature into the fire, and wasn’t harmed. <u>6</u>But they expected that he would have swollen or fallen down dead suddenly, but when they watched for a long time and saw nothing bad happen to him, they changed their minds, and said that he was a god.<br />   <u>7</u>Now in the neighborhood of that place were lands belonging to the chief man of the island, named Publius, who received us, and courteously entertained us for three days. <u>8</u>The father of Publius lay sick of fever and dysentery. Paul entered in to him, prayed, and laying his hands on him, healed him. <u>9</u>Then when this was done, the rest also who had diseases in the island came, and were cured. <u>10</u>They also honored us with many honors, and when we sailed, they put on board the things that we needed.<br />   <u>11</u>After three months, we set sail in a ship of Alexandria which had wintered in the island, whose sign was “The Twin Brothers.” <u>12</u>Touching at Syracuse, we stayed there three days. <u>13</u>From there we circled around and arrived at Rhegium. After one day, a south wind sprang up, and on the second day we came to Puteoli, <u>14</u>where we found brothers,* and were entreated to stay with them for seven days. So we came to Rome. <u>15</u>From there the brothers, when they heard of us, came to meet us as far as The Market of Appius and The Three Taverns. When Paul saw them, he thanked God, and took courage. <u>16</u>When we entered into Rome, the centurion delivered the prisoners to the captain of the guard, but Paul was allowed to stay by himself with the soldier who guarded him.<br />   <u>17</u>After three days Paul called together those who were the leaders of the Jews. When they had come together, he said to them,</p><h1>PAUL</h1><h3>I, brothers, though I had done nothing against the people, or the customs of our fathers, still was delivered prisoner from Jerusalem into the hands of the Romans, <u>18</u>who, when they had examined me, desired to set me free, because there was no cause of death in me. <u>19</u>But when the Jews spoke against it, I was constrained to appeal to Caesar, not that I had anything about which to accuse my nation. <u>20</u>For this cause therefore I asked to see you and to speak with you. For because of the hope of Israel I am bound with this chain.</h3><p>   <u>21</u>They said to him,</p><h1>THE JEWS OF ROME</h1><h4>We neither received letters from Judea concerning you, nor did any of the brothers come here and report or speak any evil of you. <u>22</u>But we desire to hear from you what you think. For, as concerning this sect, it is known to us that everywhere it is spoken against.</h4><p>   <u>23</u>When they had appointed him a day, many people came to him at his lodging. He explained to them, testifying about God’s Kingdom, and persuading them concerning Jesus, both from the law of Moses and from the prophets, from morning until evening. <u>24</u>Some believed the things which were spoken, and some disbelieved. <u>25</u>When they didn’t agree among themselves, they departed after Paul had spoken one word,</p><h1>PAUL</h1><h3>The Holy Spirit spoke rightly through Isaiah, the prophet, to our fathers, <u>26</u>saying,<br />   ‘Go to this people, and say,<br />   in hearing, you will hear,<br />   but will in no way understand.<br />   In seeing, you will see,<br />   but will in no way perceive.<br />   <u>27</u>For this people’s heart has grown callous.<br />   Their ears are dull of hearing.<br />   Their eyes they have closed.<br />   Lest they should see with their eyes,<br />   hear with their ears,<br />   understand with their heart,<br />   and would turn again,<br />   and I would heal them.’*<br />   <u>28</u>Be it known therefore to you, that the salvation of God is sent to the nations. They will also listen.</h3><p>   <u>29</u>When he had said these words, the Jews departed, having a great dispute among themselves.<br />   <u>30</u>Paul stayed two whole years in his own rented house, and received all who were coming to him, <u>31</u>preaching God’s Kingdom, and teaching the things concerning the Lord Jesus Christ with all boldness, without hindrance.</p><h5>*Acts 27:14 Or, “a northeaster”.<br />*Acts 27:28 20 fathoms = 120 feet = 36.6 meters<br />*Acts 27:28 15 fathoms = 90 feet = 27.4 meters<br />*Acts 28:1 NU reads “we”<br />*Acts 28:14 The word for “brothers” here and where context allows may also be correctly translated “brothers and sisters” or “siblings.”<br />*Acts 28:27 Isaiah 6:9-10</h5>","path":"/363","Question1":"With which character do you most identify in this story ? Why?","Question2":"What strengths and/or weaknesses do you see in this character?","Question3":"What do you think God was wanting your selected character to learn?","Question4":"What do you think God is wanting you to learn through this story? How can you cooperate with his good purposes today?","prevPath":"/362","nextPath":"/364"}},"pathContext":{}}

